@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300;400;500;600;700;800;900&display=swap);
html {
  box-sizing: border-box;
  font-size: calc(10px + 0.3vw);
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  text-align: center;
  font-family: 'Inconsolata', monospace;
  background: #f58442;
  background: linear-gradient(45deg,  #f58442 0%,#d17a47 50%,#a1480d 100%);
}

.timer {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#control-banner {
  display: -webkit-flex;
  display: flex;
}

#control-banner > * {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.control-section {
  background: none;
  border: 0;
  color: white;
  font-size: 2rem;
  text-transform: uppercase;
  background: rgba(0,0,0,0.1);
  border-bottom: 3px solid rgba(0,0,0,0.2);
  border-right: 1px solid rgba(0,0,0,0.2);
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#break-label, #session-label {
  margin-bottom: 5px;
}

.controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

button {
  background: none;
  border: 0;
  color: white;
  margin: 0px 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}

button:hover,
button:focus {
  outline: 0;
}

button:hover {
  color: bisque;
}

button > * {
  pointer-events: none;
}

.plus-minus {
  font-size: 2rem;
}

.play-pause-reset {
  font-size: 4rem;
}




.display {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#timer-label {
  font-size: 3rem;
  color: white;
}

#time-left {
  font-weight: 100;
  font-size: 16rem;
  margin: 0;
  color: white;
  text-shadow: 4px 4px 0 rgba(0,0,0,0.05);
}




